p + p {
  font-style: italic;
}

.minmax {
  position: relative;
}

.minmax::after {
  content: '';
  position: absolute;
  left: -2px;
  bottom: 3px;
  height: 3px;
  background-color: $buttonBorder;
}

.min::after {
  width: .5em;
}

.max::after {
  width: 1.5em;
}

.congrats {
  margin-top: 2em;
  font-size: 1.3em;
}